<template>
  <div class="carusel">

    <router-link to="/movie" class="main">
    <h3>Фильмы</h3>
    <img src="@/assets/images/arrow.png" alt="">
    
    </router-link>

    <carousel  :settings="settings" :breakpoints="breakpoints" :wrap-around="true">
      <template #slides class="carusel-block">
      <Slide v-for="(movie, idx) in popularMovies" 
      :key="movie.id" 
      @click="getMovie(movie, idx)">
        <img :src="getImgFull + movie.poster_path" alt="Movie Poster" class="carousel__item" />
        <router-link class="carousel__item-link" :to="'/movie/' + movie.id" >
        </router-link>
      </Slide>
      </template>
      <template #addons>
        <navigation />
      </template>
    </carousel>
    <InfoBlock
      :selectedId="selectedId"
      :selectedMovie="selectedMovie"
      :index="index"
      @close="selectedMovie = selectedId= null"
      page="/movie/"
    />
  </div>
  </template>
  
  <script>
  import 'vue3-carousel/dist/carousel.css'
  import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel'
  import { mapGetters, mapActions , mapState } from 'vuex';
  import InfoBlock from '@/components/infoBlock/infoBlock.vue'

  export default {
    name: 'Breakpoints',
    
    data(){
    return{
      selectedId: null,
      selectedMovie: null,
      index:null,
      settings: {
      itemsToShow: 10,
      snapAlign: 'center',

    },
    breakpoints: {
      320: {
        itemsToShow: 1,
        snapAlign: 'start',
      },
      500: {
        itemsToShow: 2,
        snapAlign: 'center',
      },
      // 700px and up
      700: {
        itemsToShow: 3,
        snapAlign: 'center',
      },
      // 1024 and up
      1024: {
        itemsToShow: 5,
        snapAlign: 'start',
      },
      1400: {
        itemsToShow: 6,
        snapAlign: 'start',
      },
    },
    }},
    components: {
      Carousel,
      Slide,
      Navigation,
      InfoBlock
    },
   
      computed: {
        ...mapGetters(['getImgFull', 'getImg']),
        ...mapState('popularMovies', ['popularMovies']),
        ...mapState('movie', ['movie']),
    
  },
  methods:{
    ...mapActions('popularMovies', ['getPopular']),
    ...mapActions('movie', ['getMovieById']),

    async getMovie(movie,idx){
      this.selectedId = movie.id
      this.selectedMovie = null
      this.index = idx
      await this.getMovieById(this.selectedId)
      this.selectedMovie = this.movie
    },
    },
   mounted(){
    this.getPopular()
    
  }
  
  }

  </script>