import axios from "axios";

export default {
    namespaced: true,
    state: {
      path: 'https://api.themoviedb.org/3/tv/popular?',
      popularTVs: null
    },
    mutations: {
        getPopular(state, payload){
            state.popularTVs = payload
        }
    },
    actions: {
      async getPopularTv({commit, state, rootState }, page = 1){
        try{
          const res = await axios.get(` ${state.path}api_key=${rootState.apiKey}&language=ru-RU&page=${page}`)
          const results = res.data.results
          commit('getPopular', results )
          console.log(results);
        } catch (error) {
          console.log(error);
        }
        }
      },
    getters: {},
  }