<template>
  <section class="main__rate">
    <h2 class="main__rate-title">
        ТОП
        <span>10</span>
    </h2>
    <Carousel  :settings="settings" :breakpoints="breakpoints" :wrap-around="true">
      <template #slides>
      <Slide  v-for="(movie, idx) in topRate" 
      :key="movie.id" >
        <div class="main__rate-block">
        <p class="main__rate-number">{{ idx + 1 }}</p>
        <img :src="getImgFull + movie.poster_path" alt="Movie Poster" class="main__rate-item" />
        </div>
      </Slide>
      </template>
      <template #addons>
        <navigation />
      </template>
    </Carousel>

  </section>
</template>

<script>
import 'vue3-carousel/dist/carousel.css'
import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel'
import { mapGetters, mapActions , mapState } from 'vuex';


export default {
  name: 'Breakpoints',
  
  data(){
  return{
    settings: {
    itemsToShow: 10,
    snapAlign: 'center',
  },
  breakpoints: {
    320: {
      itemsToShow: 1,
      snapAlign: 'start',
    },
    500: {
      itemsToShow: 1,
      snapAlign: 'center',
    },
    // 700px and up
    700: {
      itemsToShow: 2,
      snapAlign: 'center',
    },
    // 1024 and up
    1024: {
      itemsToShow: 3,
      snapAlign: 'start',
    },
    1400: {
      itemsToShow: 3.5,
      snapAlign: 'start',
    },
  },
  }},
  components: {
    Carousel,
    Slide,
    Navigation,
  },
 
    computed: {
     ...mapState('topRate', ['topRate']),
     ...mapGetters(['getImgFull', 'getImg']),
  
},
methods:{
...mapActions('topRate', ['getTop'])
  },
 mounted(){
    this.getTop()
  
}

}

</script>

