<template>
  <div class="main__upcoming">
    <UpcomingItem
      v-for="(movie, ind) in upcoming"
      :key="movie.id"
      :movie="movie"
      :ind="ind"
      :slideView="slideView"
      :next = "upcoming [ind + 1 == upcoming.length ? 0 : ind + 1]"
      @slideNext="slideNext"
    />
  </div>
</template>

<script>
import UpcomingItem from '@/components/upcoming/upcomingitem.vue'
import { mapGetters, mapActions} from "vuex";
export default{
  components:{UpcomingItem},
  data(){
    return{
      upcoming: null,
      slideView: 0,
      timeout: null,
    }
  },
  methods:{
    ...mapActions('upcoming', ['getUpcoming']),
    slide(){
      if(this.upcoming.length - 1 == this.slideView){
        this.slideView = 0
      }else{
        this.slideView++
      }
      this.timeout = setTimeout(this.slide, 10000)
    },
    slideNext(){
      clearTimeout(this.timeout)
      this.slide()
    }
  },
  computed:{
    ...mapGetters('upcoming', ['getUpcomingArray'])
  },
  async mounted(){
    await this.getUpcoming()
    this.upcoming = this.getUpcomingArray
    this.timeout = setTimeout(this.slide, 10000)
  }
}
</script>
