import axios from "axios";

export default {
    namespaced: true,
    state: {
      path: 'https://api.themoviedb.org/3/movie/top_rated?',
      topRate: null
    },
    mutations: {
        getTop(state, payload){
            state.topRate = payload
        }
    },
    actions: {
      async getTop({commit, state, rootState }, page = 1){
        try{
          const res = await axios.get(` ${state.path}api_key=${rootState.apiKey}&language=ru-RU&page=${page}`)
          const results = res.data.results
          const top10 = []
          results.forEach(item =>{
            if(top10.length < 10 ) top10.push(item)
          })
          console.log(top10);
          commit('getTop', top10 )
        } catch (error) {
          console.log(error);
        }
        }
      },
    getters: {},
  }