<template>
<Transition name="fade" mode="out-in">
  
   

  <div class="main__upcoming-item" v-if="slideView == ind">
    <img :src="getImgFull + movie.backdrop_path" alt="" class="upcoming-item__img">
    <div class="upcoming-item__content container">
      <h1>{{movie.title}}</h1>
      <p>{{movie.overview}}</p>
        <BtnMore :id="movie.id"/>
    </div>
    <div class="upcoming-item__next" @click="$emit('slideNext')">
      <img :src="getImg + next.backdrop_path" alt="" class="upcoming-item__next--img">
      <div class="upcoming-item__next--content">
        <p>Следующий</p>
        <h4>{{next.title}}</h4>
      </div>
      <div class="upcoming-item__next--line"></div>
    </div>
  </div>
</Transition>
</template>

<script>
import BtnMore from '@/components/UI/BtnMore.vue'
import { mapGetters, mapActions } from "vuex";
export default {
  components: {BtnMore},
  props:{
    movie: Object,
    ind: Number,
    slideView: Number,
    next: Object,
  },
  computed: {
    ...mapGetters(['getImgFull', 'getImg'])
  }
}
</script>
