<template>
    <div class="carusel">

      <router-link to="/tv" class="main">
      <h3>Сериалы</h3>
      <img src="@/assets/images/arrow.png" alt="">
      
      </router-link>
      <Carousel  :settings="settings" :breakpoints="breakpoints" :wrap-around="true">
        <template #slides>
        <Slide v-for="(tv, idx) in popularTVs" 
        :key="tv.id" 
        @click="getTv(tv, idx)">
          <img :src="getImgFull + tv.poster_path" alt="Movie Poster" class="carousel__item" />
          <router-link class="carousel__item-link" :to="'/tv/' + tv.id" >
        </router-link>
        </Slide>
        </template>
        <template #addons>
          <navigation />
        </template>
      </Carousel>
      <InfoBlock
        :selectedId="selectedId"
        :selectedMovie="selectedMovie"
        :index="index"
        @close="selectedMovie = selectedId = null"
        page="/tv/"
  
      />
    </div>
    </template>
    
    <script>
    import 'vue3-carousel/dist/carousel.css'
    import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel'
    import { mapGetters, mapActions , mapState } from 'vuex';
    import InfoBlock from '@/components/infoBlock/infoBlock.vue';
  
    export default {
      name: 'Breakpoints',
      
      data(){
      return{
        selectedId: null,
        selectedMovie: null,
        index:null,
        settings: {
        itemsToShow: 10,
        snapAlign: 'center',
  
      },
      breakpoints: {
        320: {
          itemsToShow: 1,
          snapAlign: 'start',
        },
        500: {
          itemsToShow: 2,
          snapAlign: 'center',
        },
        // 700px and up
        700: {
          itemsToShow: 3,
          snapAlign: 'center',
        },
        // 1024 and up
        1024: {
          itemsToShow: 5,
          snapAlign: 'start',
        },
        1400: {
          itemsToShow: 6,
          snapAlign: 'start',
        },
      },
      }},
      components: {
        Carousel,
        Slide,
        Navigation,
        InfoBlock
      },
     
        computed: {
          ...mapGetters(['getImgFull', 'getImg']),
          ...mapState('popularTVs', ['popularTVs']),
          ...mapState('tv', ['tv'])
      
    },
    methods:{
      ...mapActions('popularTVs', ['getPopularTv']),
      ...mapActions('tv', ['getTvById']),

      async getTv(tv,idx){
        this.selectedId = tv.id
        this.selectedMovie = null
        this.index = idx
        await this.getTvById(this.selectedId)
        this.selectedMovie = this.tv
      }
      },
     mounted(){
       this.getPopularTv()
      
    }
    
    }
  
    </script>