import axios from 'axios'
export default {
  namespaced: true,
  state: {
    movies: null
  },
  mutations: {
    GetMovies(state, payload){
      state.movies = payload
    }
  },
  actions: {
    async GetMovies({commit, state, rootState }){
      try{
        const res = await axios.get(` https://api.themoviedb.org/3/movie/now_playing?api_key=${rootState.apiKey}&language=ru-RU&page=1`)
        const results = res.data.results
        commit('GetMovies', results)
        console.log(results);
      } catch (error) {
        console.log(error);
      }
      }
    },
  getters: {
    GetMoviesArray: (state) => state.movies
  },
}